.detail-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/*글 제목*/
.detail-container h2 {
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 10px;
}

/*작성자*/
.detail-container .author {
  font-size: 14px;
  color: rgb(1, 99, 50);
  margin-bottom: 20px;
}

/*글내용*/
.detail-container .content {
  min-height: 150px;
  border: 1px solid rgb(167, 167, 167);
  border-radius: 5px;
  background-color: rgb(236, 236, 236);
  padding: 10px;
  font-size: 16px;
  white-space: pre-wrap; /* 줄바꿈 유지 */
  line-height: 1.5;
}

.list-button:hover {
  background-color: rgb(248, 170, 134);
}

.delete-button:hover {
  background-color: rgb(243, 153, 126);
}

.edit-button:hover {
  background-color: rgb(154, 143, 255);
}

/*글 수정 폼 스타일*/

.edit-form input {
  width: 100%;
  border: 1px solid #dddddd;
  margin-bottom: 10px;
  font-size: 16px;
  border-radius: 5px;
  padding: 10px;
  height: 40px;
  box-sizing: border-box;
}

.edit-form textarea {
  width: 100%;
  border: 1px solid #dddddd;
  margin-bottom: 10px;
  font-size: 16px;
  border-radius: 5px;
  padding: 10px;
  min-height: 150px;
  resize: vertical;
  box-sizing: border-box;
}

/* --------------------------- */
/*      댓글 섹션 시작         */
/* --------------------------- */
.comment-section {
  max-width: 800px;
  margin: 30px auto 0;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* 댓글 제목 */
.comment-section h3 {
  margin-bottom: 10px;
  font-size: 18px;
  color: #333;
}

/* 댓글 작성 폼 */
.comment-form textarea {
  width: 100%;
  min-height: 80px;
  border: 1px solid rgb(167, 167, 167);
  border-radius: 5px;
  background-color: rgb(236, 236, 236);
  padding: 10px;
  font-size: 16px;
  line-height: 1.5;
  box-sizing: border-box;
}

.comment-form {
  display: flex;
  flex-direction: column;
  align-items: flex-end; /* 버튼 오른쪽 정렬 */
}

.comment-form textarea {
  width: 100%;
  min-height: 80px;
  border: 1px solid rgb(167, 167, 167);
  border-radius: 5px;
  background-color: rgb(236, 236, 236);
  padding: 10px;
  font-size: 16px;
  line-height: 1.5;
  box-sizing: border-box;
  margin-bottom: 8px; /* 버튼과의 간격 */
}

.comment-form .comment-button {
  align-self: flex-end; /* 혹시 flex 부모 설정이 없을 때도 오른쪽 정렬 보장 */
  background-color: burlywood;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s;
}

.comment-form .comment-button:hover {
  background-color: rgb(209, 131, 108);
}

/* 댓글 리스트 */
.comment-list {
  list-style: none;
  margin: 20px 0 0 0;
  padding: 0;
}

.comment-item {
  border-top: 1px solid #ddd;
  padding: 15px 0;
}

.comment-item:first-child {
  border-top: none;
}

/* 댓글 헤더: 작성자, 날짜 */
.comment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: gray;
  margin-bottom: 5px;
}

.comment-author {
  font-weight: bold;
  color: rgb(1, 99, 50);
}

.comment-date {
  font-size: 13px;
  color: #888;
}

/* 댓글 내용 */
.comment-content {
  background-color: rgb(245, 245, 245);
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  white-space: pre-wrap;
  line-height: 1.5;
  margin-bottom: 8px;
}

/* 댓글 수정폼 */
.comment-item textarea {
  width: 100%;
  min-height: 80px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 8px;
  font-size: 15px;
  background-color: #f9f9f9;
  box-sizing: border-box;
}

/* 댓글 수정 저장/취소 버튼 */
.comment-save,
.comment-cancel {
  background-color: burlywood;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  margin-right: 8px;
  cursor: pointer;
  transition: 0.2s;
}

.comment-save:hover {
  background-color: rgb(81, 62, 253);
}

.comment-cancel:hover {
  background-color: rgb(209, 131, 108);
}

